import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Categories, SortPopup, PizzaBlock, PizzaLoadingBlock } from '../components'
import { setCategory, setSortBy } from '../redux/actions/filters'
import { fetchPizzas } from '../redux/actions/pizzas'
import { addPizzaToCart } from '../redux/actions/cart'

const categoryNames = ['–ú—è—Å–Ω—ã–µ', '–í–µ–≥–µ—Ç–∞—Ä–∏–∞–Ω—Å–∫–∞—è', '–ì—Ä–∏–ª—å', '–û—Å—Ç—Ä—ã–µ', '–ó–∞–∫—Ä—ã—Ç—ã–µ']
const sortItems = [
	{ name: '–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏', type: 'popular', order: 'desc' },
	{ name: '—Ü–µ–Ω–µ', type: 'price', order: 'desc' },
	{ name: '–∞–ª—Ñ–∞–≤–∏—Ç—É', type: 'name', order: 'asc' },
]

const Home = () => {
	const dispatch = useDispatch()
	const items = useSelector(({ pizzas }) => pizzas.items)
	const cartItems = useSelector(({ cart }) => cart.items)
	const isLoaded = useSelector(({ pizzas }) => pizzas.isLoaded)
	const { category, sortBy } = useSelector(({ filters }) => filters)

	React.useEffect(() => {
		dispatch(fetchPizzas(category, sortBy))
	}, [category, sortBy])

	//–∑–±–µ—Ä—ñ–≥–∞—î —Å–∏–ª–∫—É –Ω–∞ –æ–±—î–∫—Ç —â–æ–± –Ω–µ —Ä–µ—Ä–µ–Ω–¥–∏—Ä–∏—Ç–∏ –º–µ–Ω—é
	const onSelectCategory = React.useCallback(index => {
		dispatch(setCategory(index))
	}, [])

	const onSelectSortType = React.useCallback(type => {
		dispatch(setSortBy(type))
	}, [])

	const handleAddPizzaToCart = obj => {
		dispatch(addPizzaToCart(obj))
	}

	return (
		<div className='container'>
			<div className='content__top'>
				<Categories
					activeCategory={category}
					onClickCategory={onSelectCategory}
					items={categoryNames}
				/>
				<SortPopup
					activeSortType={sortBy.type}
					items={sortItems}
					onClickSortType={onSelectSortType}
				/>
			</div>
			<h2 className='content__title'>–í—Å–µ –ø–∏—Ü—Ü—ã</h2>
			<div className='content__items'>
				{isLoaded
					? items.map(item => (
							<PizzaBlock
								onClickAddPizza={handleAddPizzaToCart}
								key={item.id}
								addedCount={cartItems[item.id] && cartItems[item.id].items.length}
								{...item}
							/>
					  ))
					: Array(10)
							.fill(0)
							.map((_, index) => <PizzaLoadingBlock key={index} />)}
			</div>
		</div>
	)
}

export default Home
